.modal {
    z-index: 100;
    position: fixed;
    left: 10%;
    width: 80%;
    background-color: white;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
    border-radius: 8px;
    transition: all ease 0.5s;
  }

  .openModal{
    top: 22vh;
  }
  .closeModal{
    top:-100%;
  }
  
  .modal__header {
    width: 100%;
    padding: 1rem 0rem;
    padding-bottom: 0rem;
    background: #2a006e;
    color: white;
  }
  
  .modal__header h2 {
    margin: 0.5rem;
  }
  
  .modal__content {
    padding: 1rem 0.5rem;
    
  }
  .modal__header form{
    background-color: white;
    color:black;
    margin:0px;
  }
  
  .modal__footer {
    padding: 1rem 0.5rem;
  }
  
  @media (min-width: 0px) {
    .modal {
      left: calc(50% - 20rem);
      width: 40rem;
    }
  }
  

  